import * as React from 'react'
import moment from 'moment'
import {
  formatISO,
  getYear,
  getMonth,
  getDate,
  getHours,
  getMinutes,
} from 'date-fns'
import { useLocation } from '@reach/router'
import ReactHTMLParser from 'react-html-parser'
import { Remarkable } from 'remarkable'
import Layout from './../layout'
import { SEO, StripeBar, Banner, Button, TextContent } from './../components'
import { Card, SubmitEvent } from './../containers/Event'
import { Content, Divider } from './../containers/Event/style'
import { format } from 'date-fns'
import frLocale from 'date-fns/locale/fr'
const categoryData = {
  Youth: {
    url: '/category/youth',
    fr: 'Jeunes',
  },
  '2SLGBTQQIA': {
    url: '/category/2SLGBTQQIA',
    fr: '2SLGBTQQIA',
  },
  Indigenous: {
    url: '/category/indigenous',
    fr: 'Autochtones',
  },
}
interface IEventPageProps {
  location: {
    pathname: string
  }
  pageContext: {
    language: string
    title: string
    titleFr: string
    eventStartDate: string
    eventEndDate: string
    eventCost: string
    eventCategory: any
    eventDescription: string
    eventDescriptionFr: string
    venueAddress: string
    venuePhone: string
    venueCity: string
    venuePostalCode: string
    venueProvince: string
    venueWebsite: string
    organizerEmail: string
    organizerName: string
    organizerPhone: string
    organizerWebsite: string
    seoKeyword: string
    seoDescription: string
  }
}

export default ({
  location,
  pageContext: {
    language,
    title,
    titleFr,
    eventStartDate,
    eventEndDate,
    eventCost,
    eventDescription,
    eventDescriptionFr,
    eventCategory = [],
    eventUrl,
    venueAddress,
    venuePhone,
    venueCity,
    venuePostalCode,
    venueProvince,
    venueWebsite,
    organizerEmail,
    organizerName,
    organizerPhone,
    organizerWebsite,
    seoKeyword,
    seoDescription,
  },
}: IEventPageProps) => {
  const md = new Remarkable()
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  const handleICal = () => {}

  return (
    <Layout location={location}>
      <SEO
        pathname={location.pathname}
        title={language === 'Fr' ? titleFr : title}
        keyword={seoKeyword}
        description={seoDescription}
      />
      <Banner colors={['#397897', '#00587F', '#80D0DB']}>
        <h1>{language === 'Fr' ? titleFr : title}</h1>
      </Banner>
      <StripeBar colors={['#E58630', '#DA5027', '#D1932A', '#D16428']} />
      <Content>
        <div className='container'>
          <div className='row'>
            <div className='col-md-6 d-flex flex-column'>
              <div>
                {ReactHTMLParser(
                  md.render(
                    language === 'Fr' ? eventDescriptionFr : eventDescription
                  )
                )}
              </div>
              <Button
                type='url'
                to={eventUrl}
                urlType='external'
                className='mt-5'
                background='light'
              >
                {isFr
                  ? `En savoir plus des ${titleFr}`
                  : `Learn more about ${title}`}
              </Button>
              <Divider />
              <div className='d-flex mb-5 flex-wrap'>
                <a
                  // href={`https://www.google.com/calendar/render?action=TEMPLATE&text=${title}&details=${eventDescription}&location=${venueAddress +
                  //   ' ' +
                  //   venueCity +
                  //   '' +
                  //   venueProvince}&dates=${formatISO(new Date(eventStartDate), {
                  //   format: 'basic',
                  // })}%2F${formatISO(new Date(eventEndDate), {
                  //   format: 'basic',
                  // })}`}
                  href={`/.netlify/functions/ical?title=${title}&details=${eventDescription}&startYear=${getYear(
                    new Date(eventStartDate)
                  )}&startMonth=${getMonth(
                    new Date(eventStartDate)
                  )}&startDate=${getDate(
                    new Date(eventStartDate)
                  )}&startHours=${getHours(
                    new Date(eventStartDate)
                  )}&startMinutes=${getMinutes(new Date(eventStartDate))}`}
                  target='_blank'
                  rel='noreferrer'
                >
                  <Button background='light'>+ Google Calendar</Button>
                </a>
                <a
                  href={`/.netlify/functions/ical?title=${title}&details=${eventDescription}&startYear=${getYear(
                    new Date(eventStartDate)
                  )}&startMonth=${getMonth(
                    new Date(eventStartDate)
                  )}&startDate=${getDate(
                    new Date(eventStartDate)
                  )}&startHours=${getHours(
                    new Date(eventStartDate)
                  )}&startMinutes=${getMinutes(new Date(eventStartDate))}`}
                  target='_blank'
                  rel='noreferrer'
                >
                  <Button
                    background='light'
                    className='ml-0 ml-sm-3 mt-4 mt-sm-0'
                    onClick={handleICal}
                  >
                    + ICal Export
                  </Button>
                </a>
              </div>
              <SubmitEvent className='mt-auto' />
            </div>
            <div className='col-md-6 mt-5 mt-md-0'>
              <Card title={isFr ? 'DÉTAILS' : 'Details'}>
                <TextContent>
                  <h4>{isFr ? 'DÉBUT:' : 'START:'}</h4>
                  <p className='mt-3'>
                    {format(new Date(eventStartDate), 'MMM d @ h:mm a', {
                      locale: isFr ? frLocale : undefined,
                    })}
                  </p>
                  <h4 className='mt-3'>{isFr ? 'FIN:' : 'END:'}</h4>
                  <p className='mt-3'>
                    {format(new Date(eventEndDate), 'MMM d @ h:mm a', {
                      locale: isFr ? frLocale : undefined,
                    })}
                  </p>
                  <h4 className='mt-3'>{isFr ? 'COÛT:' : 'COST:'}</h4>
                  {eventCost ? (
                    <p className='mt-3'>
                      {isFr ? `${eventCost} $` : `$ ${eventCost}`}
                    </p>
                  ) : (
                    <p className='mt-3'>{isFr ? `GRATUIT` : `FREE`}</p>
                  )}
                  <h4 className='mt-3'>
                    {isFr ? 'CATÉGORIES D’ÉVÉNEMENTS' : 'EVENT CATEGORIES:'}
                  </h4>
                  <div className='d-flex'>
                    {eventCategory &&
                      eventCategory.map((category: any, index: number) => {
                        return (
                          <a
                            href={
                              (isFr ? '/fr' : '') + categoryData[category].url
                            }
                            key={category}
                            className='mr-3'
                          >
                            <span>
                              {isFr ? categoryData[category].fr : category}
                              {index !== eventCategory.length - 1 && <>,</>}
                            </span>
                          </a>
                        )
                      })}
                  </div>
                </TextContent>
              </Card>
              {(venueAddress ||
                venueCity ||
                venueProvince ||
                venuePostalCode) && (
                <Card title={isFr ? 'ENDROIT' : 'Venue'}>
                  <TextContent>
                    <a
                      href={`https://maps.google.com/?q=${venueAddress} ${venueCity} ${venueProvince} ${venuePostalCode}`}
                      target='_blank'
                      rel='noreferrer'
                    >
                      <span>{`${venueAddress}, ${venueCity}, ${venueProvince} ${venuePostalCode}`}</span>
                    </a>
                  </TextContent>
                </Card>
              )}
              {(organizerPhone || organizerEmail || organizerWebsite) && (
                <Card title={isFr ? 'ORGANISATEUR' : 'Organizer'}>
                  <TextContent>
                    <p>{organizerName}</p>
                    <h4 className='mt-3'> {isFr ? 'TÉLÉPHONE' : 'PHONE'}:</h4>
                    <a href='/' className='mt-3'>
                      <span>{organizerPhone}</span>
                    </a>
                    <h4 className='mt-3'>{isFr ? 'COURRIEL' : 'EMAIL'}:</h4>
                    <a href={`mailto:${organizerEmail}`} className='mt-3'>
                      <span>{organizerEmail}</span>
                    </a>
                    <h4 className='mt-3'>{isFr ? 'SITE WEB' : 'WEBSITE'}:</h4>
                    <a
                      href={organizerWebsite}
                      className='mt-3'
                      rel='noreferrer'
                      target='_blank'
                    >
                      <span>{organizerWebsite}</span>
                    </a>
                  </TextContent>
                </Card>
              )}
            </div>
          </div>
        </div>
      </Content>
    </Layout>
  )
}
