import React from 'react'
import { useLocation } from '@reach/router'
import { Container } from './style'
import { Button } from './../../../components'
import CalendarImg from './../../../images/on-the-pulse-calendar.svg'
interface ISubmitEventProps {
  className?: string
}

export default ({ className }: ISubmitEventProps) => {
  const loc = useLocation()
  const isFr = loc.pathname.startsWith('/fr/')

  return (
    <Container className={className}>
      <div className='position-relative'>
        <div className='d-flex'>
          <img src={CalendarImg} alt='calendar' className='mx-auto' />
        </div>
        <h2 className='mt-4 text-center'>
          {isFr
            ? 'Envoyez-nous le lien d’un événement à venir que vous aimeriez que nous ajoutions et nous le publierons ici! '
            : 'Submit an upcoming event you’d like us to include and we’ll post it here!'}
        </h2>
        <div className='mt-5 d-flex justify-content-center'>
          <Button type='url' to='/shareEvent'>
            {isFr
              ? 'Soumettez votre événement à venir ici'
              : 'Submit your upcoming event here'}
          </Button>
        </div>
      </div>
    </Container>
  )
}
